@import url('https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap');
html,
body {
	background-color: black;
	overflow: hidden;
	height: 100vh;
	width: 100vw;
}
span {
	display: block;
	width: 1px;
	height: 1px;
	background-color: aqua;
}
#holder {
	width: 100vw;
	overflow-x: hidden;
	overflow: hidden;
}
.imger {
	/* max-width: 50vw; */
	max-height: 61.8vh;
	/* margin-left: 50vw; */
	/* transform: translateX(-50%); */
	margin-top: 30px;
	border-radius: 20px;
}

.left {
	transform: translateX(-75%);
}
.right {
	transform: translateX(-25%);
}

::-webkit-scrollbar-track {
	-webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.9);
	border-radius: 10px;
	background-color: #cccccc;
}

::-webkit-scrollbar {
	width: 12px;
	background-color: #f5f5f5;
}

::-webkit-scrollbar-thumb {
	border-radius: 10px;
	background-color: #d62929;
	background-image: -webkit-linear-gradient(
		90deg,
		transparent,
		rgba(0, 0, 0, 0.4) 50%,
		transparent,
		transparent
	);
}

.imgDiv {
	/* background-size: contain;
	background-repeat: no-repeat;
	background-position: center;
	height: 61vh;
	width: 61vw; */
	/* margin-top: 61vh; */
	display: flex;
	align-items: center;
	justify-content: center;
	/* transform: scale(1.2); */
	overflow: hidden;
	border-radius: 20px;
}
.title {
	color: #cccccc;
	font-family: 'Great Vibes', cursive;
	font-size: 5rem;
	position: absolute;
	animation: example 3s infinite;
}
.header {
	width: 100vw;
	height: 100vh;
	display: flex;
	align-items: center;
	justify-content: center;
	background: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.7)),
		url('http://localhost:3000/images/%20(3).jpg');
	background-position: center;
	background-size: cover;
}
.border_curve {
	border-radius: 20px;
}

.gal_link {
	position: absolute;
	color: white;
	margin-top: 25vh;
	font-family: 'Great Vibes', cursive;
	font-size: 3rem;
	text-decoration: none;
	transition: 0.3s;
}
.gal_link:hover {
	font-size: 4rem;
}

.trans {
	background-color: #860129;
	position: absolute;
	width: 100vw;
}

@keyframes example {
	0% {
		font-size: 5rem;
	}
	50% {
		font-size: 5.1rem;
	}
	100% {
		font-size: 5rem;
	}
}
